import React, { Component, useState, useEffect } from "react";
//import { useState, useEffect, ChangeEvent,  } from "react";
import { HashRouter, Routes, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import Home from "./components/home.component";
import MetaMask from "./components/metamask.component";
import WalletConnect from "./components/walletconnect.component";
import { useTranslation } from 'react-i18next';
import { useParams, useLocation } from 'react-router-dom';

const App = () => {
  const { t, i18n } = useTranslation();
  const { lang } = useParams();
  const langUrl = i18n.language === '' ? '/en' : '/' + i18n.language;
  const url = useLocation();

  function changeLanguage(lng: any) {
    console.log("window.location.pathname: " + url.pathname);
    let newUrl = url.pathname.replace(`/${lang}`, `/${lng}`);
    console.log("newUrl: " + newUrl);
    i18n.changeLanguage(lng);
    //window.location.replace(newUrl);
    
  }
  
  useEffect(() => {
    console.log("useEffect: ");
    i18n.changeLanguage(lang);
  }, []);


  return (
    <div>
      <nav className="navbar navbar-expand navbar-dark bg-dark">
        <Link to={langUrl} className="navbar-brand">
          {t('title')}
        </Link>
        <div className="navbar-nav mr-auto">
          <li className="nav-item">
            <Link to={langUrl + "/metamask"} className="nav-link">
              MetaMask
            </Link>
          </li>
          <li className="nav-item">
            <Link to={langUrl + "/walletconnect"} className="nav-link">
              WalletConnect
            </Link>
          </li>
        </div>
        <ul className="navbar-nav ms-auto">
          <li className="nav-item">
            <button onClick={() => changeLanguage("zh-TW")} className="btn btn-light">中文</button>
            <button onClick={() => changeLanguage("en")} className="ms-1 me-1 btn btn-light">English</button>
          </li>
        </ul>
      </nav>

      <Routes>
        <Route path={"/"} element={<Home/>} />
        <Route path={"/metamask"} element={<MetaMask/>} />
        <Route path={"/walletconnect"} element={<WalletConnect/>} />
      </Routes>
    </div>
  )
}

export default App